/*
# My Portfolio
*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  min-height: 600px;
}

body {
  height: 100%;
  min-height: 600px;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.4;
  color: #CBD5E1;
}

.main {
  height: 100%;
  min-height: 600px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
}
.main .avatar-wrapper {
  z-index: 999;
  width: fit-content;
  padding: 5px;
  margin-right: 20px;
}
.main .avatar-wrapper .avatar {
  width: 100%;
  height: auto;
  border-radius: 50%;
  -webkit-box-shadow: 0px 0px 25px 3px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 25px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 25px 3px rgba(0, 0, 0, 0.5);
}
.main .details-wrapper {
  z-index: 999;
  padding: 5px;
}
.main .details-wrapper .heading-wrapper h1 {
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.6;
  color: #E2E8F0;
  text-shadow: 1px 1px 3px #000000;
}
.main .details-wrapper .typed-wrapper span {
  font-style: italic;
  text-shadow: 1px 1px 3px #000000;
}
.main .details-wrapper .social-icons-wrapper {
  margin-top: 10px;
}
.main .details-wrapper .social-icons-wrapper .social-icons {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.main .details-wrapper .social-icons-wrapper .social-icons li {
  margin-right: 8px;
}
.main .details-wrapper .social-icons-wrapper .social-icons li:last-child {
  margin-right: 0;
}
.main .details-wrapper .social-icons-wrapper .social-icons li a {
  display: inline-block;
  width: 48px;
  height: 48px;
}
.main .details-wrapper .social-icons-wrapper .social-icons li a img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  -webkit-box-shadow: 0px 0px 25px 1px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 25px 1px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 25px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.4s linear;
  -moz-transition: all 0.4s linear;
  -o-transition: all 0.4s linear;
  -ms-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.main .details-wrapper .social-icons-wrapper .social-icons li a img:hover, .main .details-wrapper .social-icons-wrapper .social-icons li a img:focus {
  -webkit-transform: scale(0.9);
  -moz-transform: scale(0.9);
  -o-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}

canvas {
  display: block;
}

#particles-js {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: #0f2027;
  background: -webkit-linear-gradient(to right bottom, #0f2027, #203a43, #2c5364);
  background: -moz-linear-gradient(to right bottom, #0f2027, #203a43, #2c5364);
  background: -o-linear-gradient(to right bottom, #0f2027, #203a43, #2c5364);
  background: -ms-linear-gradient(to right bottom, #0f2027, #203a43, #2c5364);
  background: linear-gradient(to right bottom, #0f2027, #203a43, #2c5364);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

@media (max-width: 992px) {
  .main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main .avatar-wrapper {
    margin-right: 0;
  }
  .main .details-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
}
@media (max-width: 576px) {
  .main .details-wrapper .typed-wrapper {
    min-height: 46px;
  }
}